.App
{
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
        -ms-transform: translateX(-50%) translateY(-50%);
            transform: translateX(-50%) translateY(-50%);
}

.App__gameFieldWrapper
{
    border: 1px solid rgba(0, 0, 0, .1);
}

.App__mineLeftCount
{
    font-size: 24px;
    margin: 5px;
    text-align: center;
}



